
.vd-table-card{
    display:grid;
    grid-template-rows: auto 1fr;
    overflow:hidden;
}
.vd-table{
    display:grid;
    overflow: hidden;
    grid-template-rows: auto 1fr;
    margin:0px;
    padding:0px;
    max-width:100%;
    max-height:100%;
}
.vd-table-header{
    width:100%;
    display:grid;
    text-align:left;
    border-bottom: 1px solid rgba(0,0,0,.12);;
    cursor: pointer;
    align-self:center;
    padding: 5px 15px;
    font-weight: 500;
    font-size: 12px;
}
.vd-table-header-cell{
    /*flex: 1 1 auto;
    flex-grow: 0;*/
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    color: rgba(0,0,0,.54);
    padding:0px 7px;
}
.vd-table-header-cell-text{
}
.vd-table-header-cell-icon{
    padding:3px;
    width:24px;
}
.vd-table-body{
    width: 100%;
    -webkit-box-flex: 1;
        -ms-flex: 1 1 100%;
            flex: 1 1 100%;
    margin:0px;
    padding:0px;
    text-align: center!important;
    overflow-y: auto;
    max-height: 100%;
    max-width: 100%;
}
.vd-table-body-row{
    width:100%;
    display:grid;
    text-align: left;
    border-bottom: 1px solid rgba(0,0,0,.12);
    cursor: pointer;
    align-self:center;
    padding: 5px 15px;
    font-weight: 400;
    font-size: 13px;
}
.vd-table-body-row-active, .vd-table-body-row:hover{
    background: #eee;
}
.vd-table-body-row-cell{
    -ms-flex-item-align: center;
        align-self: center;
    padding: 2px 5px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}



